@import './styles/dive.scss';
@import './styles/cocktail.scss';
@import './styles/rooftop.scss';
@import './styles/natty.scss';

body {
    background-color: black;
    font-family: 'Courier New', Courier, monospace;
    display: flex;
    justify-content: space-around;
    align-items: center;
    // flex-direction: column;
}
.splash {
    font-size: x-large;
    position:fixed;
    top: 50%;
    left: 50%;
    height: 98%;
    width: 98%;
    transform: translate(-50%, -50%);
    background-color: black;
    transition: all ease-in-out 600ms;
    z-index: 10;
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    // justify-content: space-around;
}

#splash_row {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    
}

#splash_button {
    background-color: white;
    display: flex;
    flex-direction: column;
    margin: 20px;
}
// .hidden {
//     transition: 0.5s;
//     display: none;
// }

#map {
    height: 500px;
    min-width: 520px;
    border-radius: 15px;
    border: 10px solid white;
}

h1, h2 {
    color: white;
    font-family: 'Rock Salt', cursive;
}

#info {
    display: flex;
    border-radius: 15px;
    align-items: center;
    border: 10px solid white;
    background-color: rgb(181, 181, 181);
    width: 500px;
    height: 240px;
    margin-left: 20px;
    margin-bottom: 20px;
    padding: 10px;
    position: relative;
}

#info_overlay {
    color: white;
    font-family: 'Rock Salt', cursive;
    font-size: xx-large;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: inherit; 
    width: 490px;
    height: 220px;
    padding: inherit;
    position: absolute;
    z-index: 1;
}

.info_flash {
    -webkit-animation: fadeEffect 1s;
    animation: fadeEffect 1.5s;
}

#photo {
    display: flex;
    align-items: center;
    margin-right: 10px ;
}

#photo .pic {
    width: 200px;
    max-height: 240px;
}

#title .pic {
    height: 90px; 
    max-width: 290px;
}

#bop {
    font-size: xxx-large;
    display: flex;
    border-radius: 15px;
    align-items: center;
    justify-content: center;
    border: 10px solid white;
    background-color: rgb(181, 181, 181);
    width: 500px;
    height: 180px;
    margin-left: 20px;
    padding: 10px;
    position: relative;
    // z-index: 1;
}

#bop_overlay {
    color: white;
    font-family: 'Rock Salt', cursive;
    font-size: x-large;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: inherit; 
    width: 490px;
    height: 160px;
    padding: inherit;
    position: absolute;
    z-index: 1;
    // transition: all ease-in-out 600ms;
}
.container {
    display: flex;
    flex-direction: column;
}

.navbar {
    display: flex;
    width: 1098px;
    justify-content: space-between;
}

.main {
    display: flex;
}

.controls {
    border-radius: 15px;
    border: 10px solid white;
    background-color: rgb(181, 181, 181);
    width: 306px;
    height: 50px;
    margin-right: 20px;
    margin-bottom: 20px;
}

.controls .vibe {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 14px 16px;
    transition: 0.3s;
    font-size: 17px;
}

.controls button:hover {
    background-color: rgb(145, 145, 145);

  }

.controls button#active {
    background-color: rgb(78, 78, 78);
}

@-webkit-keyframes fadeEffect {
from {opacity: 0;}
to {opacity: 1;}
}

@keyframes fadeEffect {
from {opacity: 0;}
to {opacity: 1;}
}

.author {
    display: flex;
    border-radius: 15px;
    justify-content: space-around;
    align-items: center;
    border: 10px solid white;
    background-color: rgb(181, 181, 181);
    width: 330px;
    height: 50px;
    margin-left: 20px;
}

.links {
    width: 50%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 5px;
}

.ian {
    display: flex;
    align-items: center;
    margin-right: 10px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: xx-large;
}


